/* typography vars */
@import url("https://fonts.googleapis.com/css?family=Nunito+Sans:300,400,600,700|Open+Sans:300,400,600,700&display=swap");
/* Basic config */
:root {
  --orange-brand: #FF9F43;
  --yellow-brand: #FFBF2E;
  --cyan-brand: #01A3A4;
  --blue-brand: #00316E;
  --background-color: #FAFAFA;
  --text-color: #707070;
  --heading-color: #202020;
  --text-line-height: 1.6;
  --font-primary: Open Sans, sans-serif;
  --font-display: Nunito Sans, sans-serif;
  --font-size-small: 1.4rem;
  --font-size-medium: 1.7rem;
  --font-size-large: 2rem;
  --font-size-heading: 3rem;
  --shadow-dark: 0 2rem 6rem rgba(0,0,0, .4);
  --shadow-light: 0 2rem 6rem rgb(255, 255, 255, .3);
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-size: var(--font-size-small);
  line-height: var(--text-line-height);
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: var(--font-primary);
  font-weight: 300;
}

.site-container {
  display: grid;
  grid-template-rows: 6rem 80vh 15vh repeat(2, min-content) 40vw repeat(7, min-content);
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
}

a {
  color: #000;
  text-decoration: none;
  word-wrap: break-word;
}

a:hover, a:focus, a:active, a:visited, a:link {
  text-decoration: none;
  outline: 0;
}

hr {
  margin-top: 3rem;
  margin-bottom: 3.5rem;
  border-width: 0;
  border-top: 1px solid #ccc;
}

p {
  margin: 1.2rem 0;
}

ul {
  padding: 0;
  list-style: none;
}

ol {
  padding-left: 0;
  margin-top: 0;
}

li {
  margin-bottom: 1rem;
}

img,
video {
  display: block;
  max-width: 100%;
}

figure {
  margin: 0 auto;
  padding-bottom: 2.4rem;
}

figure img {
  display: block;
  margin: 0 auto;
}

caption,
figcaption {
  font-size: 1rem;
  font-style: italic;
  text-align: center;
}

table {
  border-collapse: collapse;
  border-color: gray;
  border-spacing: 2px;
  max-width: 100%;
  width: 100%;
}

table thead th {
  vertical-align: bottom;
  border-bottom: 1px solid #ccc;
}

table th,
table td {
  padding: 0.6rem;
  vertical-align: top;
  border-top: 1px solid #ccc;
}

table th,
table tfoot td {
  font-weight: 700;
  text-align: left;
}

table td {
  border-bottom: 1px solid #ccc;
  padding: .6rem;
  text-align: left;
  vertical-align: top;
}

textarea,
select {
  height: 38px;
  padding: 6px 10px;
  background-color: #fff;
  border: 1px solid #e2e2e2;
  box-shadow: none;
}

textarea:focus,
select:focus {
  border: 1px solid #9534f0;
  outline: 0;
}

input[type="email"], input[type="number"], input[type="search"], input[type="text"], input[type="tel"], input[type="url"], input[type="password"] {
  appearance: none;
  background-color: #fff;
  border: 1px solid #e2e2e2;
  box-shadow: none;
  height: 38px;
  padding: 6px 10px;
  -moz-appearance: none;
  -webkit-appearance: none;
}

input[type="email"]:focus, input[type="number"]:focus, input[type="search"]:focus, input[type="text"]:focus, input[type="tel"]:focus, input[type="url"]:focus, input[type="password"]:focus {
  outline: 0;
  border: 1px solid #604ec957;
}

textarea {
  appearance: none;
  min-height: 65px;
  padding-bottom: 6px;
  padding-top: 6px;
  -moz-appearance: none;
  -webkit-appearance: none;
}

label,
legend {
  display: block;
  font-weight: 600;
  margin-bottom: .5rem;
}

fieldset {
  padding: 0;
  border-width: 0;
}

input[type="checkbox"], input[type="radio"] {
  display: inline;
}

label > .label-body {
  display: inline-block;
  font-weight: normal;
  margin-left: .5rem;
}

pre {
  overflow: auto;
}

code {
  background: gainsboro;
  border-radius: 50%;
  border: 1px solid #ccc;
  font-size: 90%;
  margin: 0 .2rem;
  padding: .2rem .5rem;
  white-space: nowrap;
}

pre > code {
  display: block;
  padding: 1rem 1.5rem;
  white-space: pre;
}

.container {
  width: 1160px;
  margin: 0 auto;
  padding: 0 2rem;
  max-width: 100%;
}

/* typography */
.heading-1, .heading-2, .heading-3, .heading-4 {
  font-family: var(--font-display);
  font-size: var(--font-size-heading);
  font-weight: 400;
  color: var(--heading-color);
}

.heading-2 {
  --font-size-heading: 4rem;
}

.heading-2--light {
  text-align: center;
  font-weight: 600;
  line-height: 1;
  width: 60%;
  margin-bottom: 2rem;
  --heading-color: #FAFAFA;
}

.heading-2--dark {
  text-align: left;
  font-weight: 600;
  line-height: 1;
  width: 60%;
  margin-bottom: 2rem;
  --heading-color: #FFBF2E;
}

.heading-4 {
  font-variant: small-caps;
  --font-size-heading: 1.6rem;
}

.heading-4--light {
  letter-spacing: 1px;
  --heading-color: #707070;
}

.heading-4--dark {
  --heading-color: #171717;
}

.page-section {
  margin: 15rem 0;
}

.btn {
  background-color: transparent;
  color: #fff;
  border: none;
  border-radius: 0;
  font-family: var(--font-display);
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  font-weight: 600;
  cursor: pointer;
  transition: all .2s;
}

.btn--orange {
  background-color: var(--orange-brand);
}

.btn--orange:hover {
  background-color: #ffac5d;
}

.btn--inverse {
  border: 2px solid var(--orange-brand);
  padding: 1.5rem 4rem;
  border-radius: 50px;
}

.btn--inverse:hover {
  background-color: #ffac5d;
}

.header {
  background-color: #2B6EB1;
  grid-column: full-start / full-end;
  position: sticky;
  z-index: 1020;
  top: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
}

.logo {
  grid-column: col-start 1 / col-end 1;
  z-index: 990;
  width: 10rem;
  height: 10rem;
}

.nav-menu {
  grid-column: col-start 5 / col-end 8;
}

.nav-menu__items {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 6rem;
}

.nav-menu__item {
  font-size: 1.2rem;
  margin-bottom: 0px;
  text-transform: uppercase;
}

.nav-menu__item:not(:last-child) {
  margin-right: 2rem;
}

.nav-menu__link {
  color: #fff;
  font-weight: 400;
  padding: 2.25rem 1rem;
}

.nav-menu__link:hover {
  background-color: #00316E;
  color: #FAFAFA;
}

.large-hero {
  grid-column: full-start / full-end;
  background: url("/assets/images/hero-3.jpg") no-repeat bottom 90% center/cover;
  background-attachment: fixed;
  color: white;
  display: grid;
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  align-items: center;
}

.large-hero__content {
  grid-column: col-start 1 / col-end 5;
}

.large-hero__title {
  color: #ffac5d;
  line-height: 1;
  margin: .5rem 0 4rem;
  font-size: 7vw;
}

@media screen and (min-width: 37.5em) {
  .large-hero__title {
    font-size: 5vw;
  }
}

@media screen and (min-width: 50em) {
  .large-hero__title {
    font-size: 4vw;
  }
}

@media screen and (min-width: 65em) {
  .large-hero__title {
    font-size: 2.5vw;
  }
}

.large-hero__subtitle {
  font-family: "Nunito Sans", sans-serif;
  color: #FAFAFA;
  font-style: italic;
}

.large-hero__description {
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

.info {
  background-color: #001C57;
  grid-column: full-start / full-end;
}

.features {
  background-color: #FAFAFA;
  grid-column: center-start / center-end;
  align-items: start;
  display: grid;
  grid-template-columns: repeat(1, minamax(min-content, 1fr));
  grid-gap: 3rem;
}

@media screen and (min-width: 37.5em) {
  .features {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 5rem;
  }
}

@media screen and (min-width: 65em) {
  .features {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 6rem;
  }
}

.feature {
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-column-gap: 1rem;
  grid-row-gap: .3rem;
}

.feature__icon {
  fill: var(--orange-brand);
  width: 8rem;
  height: 8rem;
  grid-row: 1 / span 2;
  align-self: start;
  transform: translateY(-2.7rem);
  transition: .3s all ease-out;
}

.feature__icon:hover {
  fill: var(--yellow-brand);
  transform: translateY(-3.3rem) scale(1.05);
}

.feature__text {
  font-size: var(--font-size-small);
}

.team__cta {
  grid-column: full-start / full-end;
  height: 40vh;
  background: linear-gradient(to right, rgba(39, 53, 8, 0.7), rgba(2, 2, 2, 0.7), rgba(0, 0, 0, 0.5)), url("/assets/images/cta-5.jpg") left 30% bottom 60%/cover no-repeat, #020202;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.team__cta p {
  width: 60rem;
  color: #FAFAFA;
  font-size: var(--font-size-small);
  text-align: center;
  margin: 1rem 0 4rem 0;
}

.team__members {
  background-color: #171717;
  grid-column: full-start / col-end 2;
}

.team__content {
  background-color: #00316E;
  grid-column: 4 / full-end;
}

.tours__activites {
  background-color: #001C57;
  grid-column: center-start / center-end;
}

.tours__cta {
  grid-column: full-start / full-end;
  height: 50vh;
  background: linear-gradient(to right, rgba(39, 53, 8, 0.7), rgba(2, 2, 2, 0.7), rgba(0, 0, 0, 0.5)), url("/assets/images/cta-7.jpg") left 30% bottom 30%/cover no-repeat, #020202;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tours__cta p {
  width: 60%;
  color: #FAFAFA;
  font-size: var(--font-size-medium);
  text-align: left;
  margin: 1rem 0 4rem 0;
}

.testimonals__video {
  background-color: #287899;
  grid-column: center-start / col-end 4;
}

.testimonals__content {
  background-color: #FFBF2E;
  grid-column: col-start 5 / center-end;
}

.testimonals__counter {
  background-color: #A7C9D3;
  grid-column: center-start / center-end;
}

.testimonals__slider {
  background-color: #01A3A4;
  grid-column: center-start / center-end;
  height: 30vh;
}

.gallery {
  background-color: #171717;
  grid-column: full-start / full-end;
}

.footer {
  background-color: #FFBF2E;
  grid-column: full-start / full-end;
}

.bg--light {
  background-color: #fff;
}

.bg--dark {
  background-color: #000;
}
